require('./bootstrap');
require('../../node_modules/bootstrap-select/dist/js/bootstrap-select.min');

document.addEventListener("DOMContentLoaded", () => {
    console.log("test1");
    const ingredients=document.querySelector('[name="ingredientSelector"][multiple]');
    const types=document.querySelector('[name="typetSelector"][multiple]');
    const maxTime=document.querySelector('[id="timeRange"]');
    const apply_filter=document.querySelector('[name="apply_filter"]');
    const ingredientContainer=document.querySelector('[id="IngredientsSelect"]');
    const recipesContainer=document.querySelector('[id="recipeItems"]');
    console.log("test2");
    types.addEventListener('change', (event) => {
        getData('/filters/ingredients', { types })
        .then(data => {
            ingredientContainer.innerHTML=data; 
        }).catch((error) => {
            console.error('Error:', error);
        });
    });
    apply_filter.addEventListener('click', (event) => {
        console.log("test");
        alert("test");
        filter();
    });

});
async function getData(url = '', data = {}) {
    const response = await fetch(url, {
        method: 'POST', 
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-Type': 'application/json'
    },
    body: data
    });
    return response.HTML; 
}


function filter(){
    if(ingredients.selectedOptions.length<=0){
        if (types.selectedOptions.selectedOptions.length>0) {
            getData('/filters/recipes/types', { types: types, time: maxTime })
            .then(data => {
                recipesContainer.innerHTML =data; 
            }).catch((error) => {
                console.error('Error:', error);
            });
        }
    }else{
        getData('/filters/recipes/ingredients', { ingredients: ingredients, time: maxTime  })
        .then(data => {
            recipesContainer.innerHTML =data; 
        }).catch((error) => {
            console.error('Error:', error);
        });
    }
}

